:root {
  --pink: #f5cad7;
  --pink-2: #fbe3ea;
  --butter: #ffe79a;
  --ink: #1a1a1a;
  --muted: #6b7280;
  --card: #fff;
  --max: 1000px;
  --radius: 18px;
  --shadow: 0 10px 30px #00000012;
}

* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: .1px;
  color: var(--ink);
  background: linear-gradient(180deg, var(--pink-2), #fff 40%);
  font: 16px / 1.7 ui-rounded, system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  max-width: var(--max);
  margin: auto;
  padding: clamp(16px, 2vw, 24px);
}

.site-header {
  z-index: 20;
  backdrop-filter: saturate(150%) blur(6px);
  background: linear-gradient(90deg, #ff8ec5f2, #ffcf6cf2);
  border-bottom: 1px solid #0000000d;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
  align-items: center;
  gap: 1rem;
  display: flex;
  position: sticky;
  top: 0;
}

.brand {
  color: #fff;
  letter-spacing: .3px;
  font-size: clamp(1.6rem, 3.2vw, 2.05rem);
  font-weight: 900;
  text-decoration: none;
}

.site-nav {
  flex-wrap: wrap;
  gap: .9rem;
  margin-left: auto;
  display: flex;
}

.site-nav a {
  color: var(--ink);
  background: #ffffffa6;
  border-radius: 999px;
  padding: .4rem .6rem;
  text-decoration: none;
  transition: transform .2s;
  position: relative;
}

.site-nav a:after {
  content: "";
  transform-origin: 0;
  background: linear-gradient(90deg, #ff8ec5, #ffcf6c);
  height: 2px;
  transition: transform .25s;
  position: absolute;
  bottom: .25rem;
  left: .6rem;
  right: .6rem;
  transform: scaleX(0);
}

.site-nav a:hover {
  transform: translateY(-1px);
}

.site-nav a:hover:after {
  transform: scaleX(1);
}

.site-nav a.is-active {
  outline: 2px solid #ffffffd9;
}

#progress {
  background: linear-gradient(90deg, #ff7ab6, var(--butter));
  z-index: 30;
  width: 0%;
  height: 3px;
  position: fixed;
  inset: 0 auto auto 0;
}

.xp-link {
  color: inherit;
  text-decoration: none;
  display: block;
}

.xp-link:hover .card-inner, .xp-link:focus .card-inner {
  transform: rotateY(180deg);
}

#cursor-friend {
  mix-blend-mode: multiply;
  filter: blur(1px);
  pointer-events: none;
  z-index: 25;
  opacity: .9;
  background: radial-gradient(circle at 35% 35%, #fff, #ffd0ea 55%, #0000 70%), radial-gradient(circle at 70% 70%, #fff, #ffe39f 55%, #0000 70%);
  border-radius: 999px;
  width: 36px;
  height: 36px;
  position: fixed;
  transform: translate(-50%, -50%);
}

.hero {
  grid-template-columns: 1.1fr .9fr;
  align-items: center;
  gap: 2rem;
  min-height: 52vh;
  display: grid;
}

.gradient {
  color: #0000;
  background: linear-gradient(90deg, #ff8ec5, #ff7ab6, #ffcf6c) text;
}

.anim-shimmer {
  background-size: 250% 100%;
  animation: 6s ease-in-out infinite shimmer;
}

@keyframes shimmer {
  0%, 100% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }
}

.lede {
  color: var(--muted);
  font-size: 1.05rem;
}

.cta-row {
  gap: .8rem;
  margin-top: .8rem;
  display: flex;
}

.btn {
  color: #fff;
  box-shadow: var(--shadow);
  background: #ff7ab6;
  border-radius: 999px;
  padding: .7rem 1rem;
  font-weight: 600;
  text-decoration: none;
  transition: transform .15s, box-shadow .2s, opacity .2s;
  display: inline-block;
}

.btn:hover {
  transform: translateY(-2px);
}

.btn.ghost {
  color: #ff7ab6;
  background: none;
  border: 2px solid #ff7ab6;
}

.avatar-stack {
  width: fit-content;
  margin-left: auto;
  position: relative;
}

.blob {
  filter: blur(20px);
  opacity: .7;
  z-index: -1;
  background: radial-gradient(60% 60% at 40% 40%, #ffe0ef 0%, #ffd8a6 60%, #0000 70%);
  border-radius: 50%;
  width: clamp(220px, 36vw, 420px);
  height: clamp(220px, 36vw, 420px);
  animation: 12s ease-in-out infinite alternate blob;
  position: absolute;
  inset: auto -16% -10%;
}

@keyframes blob {
  0% {
    transform: translateY(0)scale(1);
  }

  100% {
    transform: translateY(-10px)scale(1.05);
  }
}

.avatar-wrap {
  aspect-ratio: 1;
  width: clamp(200px, 30vw, 360px);
  box-shadow: var(--shadow);
  border: 6px solid var(--butter);
  will-change: transform;
  border-radius: 50%;
  overflow: hidden;
  transform: translateZ(0);
}

.float {
  animation: 6s ease-in-out infinite float;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-8px);
  }
}

.hero-img {
  object-fit: cover;
  object-position: center 30%;
  width: 100%;
  height: 100%;
  transform: scale(.999);
}

.section {
  margin-top: 2.2rem;
}

.section h2 {
  display: inline-block font-weight: 800;
  margin: .2rem 0 1rem;
  font-size: 2rem;
  position: relative;
}

.section h2:after {
  content: "";
  background: var(--butter);
  z-index: -1;
  border-radius: 6px;
  height: 8px;
  position: absolute;
  bottom: -6px;
  left: 0;
  right: -6px;
}

#about p {
  font-size: 1.15rem;
  line-height: 1.8;
}

.xp-grid {
  grid-template-columns: repeat(2, minmax(300px, 1fr));
  gap: 1.2rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  position: relative;
}

.xp-flip {
  perspective: 1100px;
  position: relative;
}

.card-inner {
  width: 100%;
  height: 260px;
  transform-style: preserve-3d;
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  transition: transform .85s cubic-bezier(.2, .85, .2, 1), filter .2s;
  position: relative;
}

.xp-flip:hover .card-inner, .xp-flip:focus-within .card-inner {
  transform: rotateY(180deg);
}

.card-face {
  border-radius: var(--radius);
  backface-visibility: hidden;
  background: var(--card);
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  padding: 1.1rem;
  display: flex;
  position: absolute;
  inset: 0;
}

.card-front img {
  object-fit: contain;
  flex: none;
  width: 70px;
  height: 70px;
}

.face-meta h3 {
  margin: 0 0 .2rem;
}

.face-meta .muted {
  margin: 0;
}

.card-back {
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: auto;
  transform: rotateY(180deg);
}

.card-back h3 {
  margin: 0 0 .25rem;
}

.card-back .date {
  color: #d27aa3;
  margin: .1rem 0 .4rem;
  font-weight: 600;
}

.stickers {
  z-index: -1;
  position: absolute;
  inset: -40px -30px -30px;
  overflow: hidden;
}

.sticker {
  opacity: .7;
  font-size: clamp(28px, 5vw, 42px);
  animation: 6s ease-in-out infinite alternate wobble;
  position: absolute;
}

.sticker:first-child {
  top: 40%;
  left: 5%;
}

.sticker:nth-child(2) {
  animation-duration: 7.5s;
  top: 10%;
  left: 85%;
}

.sticker:nth-child(3) {
  animation-duration: 8s;
  top: 65%;
  left: 70%;
}

.sticker:nth-child(4) {
  animation-duration: 6.5s;
  top: 5%;
  left: 15%;
}

@keyframes wobble {
  0% {
    transform: translateY(0)rotate(-6deg);
  }

  100% {
    transform: translateY(-18px)rotate(6deg);
  }
}

.sparkle {
  pointer-events: none;
  z-index: 10;
  filter: drop-shadow(0 2px 4px #00000026);
  width: 14px;
  height: 14px;
  animation: .7s forwards sparkle-pop;
  position: absolute;
  transform: translate(-50%, -50%)scale(0);
}

.sparkle:before {
  content: "";
  border-radius: 50%;
  position: absolute;
  inset: 0;
}

.sparkle.star:before {
  background: radial-gradient(circle, #fff, #ff8ec5 60%);
}

.sparkle.heart:before {
  background: radial-gradient(circle, #fff, #ffe39f 60%);
}

.sparkle.dot:before {
  background: radial-gradient(circle, #fff, #c8f6ff 60%);
}

@keyframes sparkle-pop {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%)scale(.2);
  }

  40% {
    opacity: 1;
  }

  100% {
    transform: translate(var(--dx, 40px), var(--dy, -40px)) scale(1);
    opacity: 0;
  }
}

.proj-grid {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.proj-card {
  background: var(--card);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  transition: transform .18s, box-shadow .2s, opacity .25s;
  overflow: hidden;
  transform: translateY(0);
}

.proj-card:hover {
  transform: translateY(-4px);
}

.proj-link {
  color: inherit;
  text-decoration: none;
  display: block;
}

.proj-card figure {
  aspect-ratio: 16 / 10;
  margin: 0;
  overflow: hidden;
}

.proj-card figure img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: transform .4s;
}

.proj-card:hover figure img {
  transform: scale(1.03);
}

.proj-meta {
  padding: 1rem;
}

.muted {
  color: var(--muted);
}

.contact a.themed {
  color: #ff7ab6;
  border-bottom: 2px solid #ff7ab659;
  padding-bottom: 2px;
  text-decoration: none;
  transition: color .2s, border-color .2s;
}

.contact a.themed:hover {
  color: #ff8ec5;
  border-color: #ffcf6c;
}

.site-footer {
  color: var(--muted);
  padding-bottom: 3rem;
}

.reveal {
  opacity: 0;
  filter: blur(2px);
  will-change: opacity, transform, filter;
  transform: translateY(16px)scale(.98);
}

.reveal.is-visible {
  opacity: 1;
  filter: none;
  transition: opacity .7s, transform .7s, filter .7s;
  transform: none;
}

.stagger-child {
  opacity: 0;
  filter: blur(1px);
  animation: .55s cubic-bezier(.2, .8, .2, 1) forwards pop;
  transform: translateY(12px)scale(.98);
}

@keyframes pop {
  to {
    opacity: 1;
    filter: none;
    transform: none;
  }
}

#toTop {
  z-index: 50;
  color: #fff;
  cursor: pointer;
  width: 42px;
  height: 42px;
  box-shadow: var(--shadow);
  opacity: 0;
  pointer-events: none;
  background: linear-gradient(135deg, #ff8ec5, #ffcf6c);
  border: none;
  border-radius: 999px;
  font-weight: 700;
  transition: opacity .25s, transform .25s;
  position: fixed;
  bottom: 16px;
  right: 16px;
  transform: translateY(8px);
}

#toTop.is-visible {
  opacity: 1;
  pointer-events: auto;
  transform: none;
}

@media (width <= 900px) {
  .hero {
    text-align: center;
    grid-template-columns: 1fr;
  }

  .hero-text {
    order: 2;
  }

  .avatar-stack {
    order: 1;
    margin: 0 auto;
  }
}

@media (width <= 760px) {
  .xp-grid {
    grid-template-columns: 1fr;
  }

  .card-inner {
    height: 280px;
  }
}
